import React from 'react';
import {AR, registerOnCameraUpdateListener} from '../misc/AR';
import {Universe} from '../game/Universe';
import * as Utils from '../misc/Utils';
import './App.css';
import {RESOURCES} from "../data/resourceData";
import {createDemoUniverse, initialDialog} from "../exploration/demoUniverse";
import TauPrimeMenu from "./TauPrimeMenu";
import icon_menu from "../data/icons/icon_menu.png";

export default class App extends React.Component {
    constructor(props) {
        super(props);

        AR.setEnabledCamera(true);

        console.assert(App.COMPONENT === null);
        App.COMPONENT = this;

        Universe.addTheUniverseListener(() => {
            console.log("Universe updated");
            this.setState({universeUpdatedTrigger : this.state.universeUpdatedTrigger + 1});
        });

        this.currentFullyLockedId = null;
        this.currentFlash = 0;

        this.presence = [];
        for (let i = 0; i < AR.NUM_MARKERS; i++)
            this.presence[i] = 0.0;

        this.state = {
            universeUpdatedTrigger: 0,
            currentlyDisplayedDialog: null,
            modalDialog: false,
            lockingId: null,
            fullyLockedId: this.currentFullyLockedId,
            flash: this.currentFlash,
            confidence: 0,
        };

        /*let periodicLogging = () => {
            console.log(this.state.hoveringId, this.state.confidence);
            this.periodicLoggingTimeout = setTimeout(periodicLogging, 3000);
        };

        this.periodicLoggingTimeout = setTimeout(periodicLogging, 3000);*/

        registerOnCameraUpdateListener((dt) => this.onUpdateCamera(dt));
    }

    onUpdateCamera(dt) {
        //
        // Boost visible markers
        //
        let markerVisibility = AR.getCurrentMarkerVisibility();
        for (let i = 0; i < AR.NUM_MARKERS; i++) {
            if (markerVisibility[i]) {
                this.presence[i] += 8.0 * dt;

                if (this.presence[i] > 1.0) {
                    this.presence[i] = 1.0;
                }
            } else {
                this.presence[i] -= 1.0 * dt;

                if (this.presence[i] < 0) {
                    this.presence[i] = 0;
                }
            }
        }

        //
        // Select most visible marker
        //
        let maxPresence = 0.0;
        let maxId = null;
        for (let i = 0; i < AR.NUM_MARKERS; i++) {
            if (this.presence[i] > maxPresence) {
                maxId = i;
                maxPresence = this.presence[i];
            }
        }

        //
        // Calculate minimum difference
        //
        let minDifference = 1.0;
        if (maxId !== null) {
            for (let i = 0; i < AR.NUM_MARKERS; i++) {
                if (i !== maxId && maxPresence - this.presence[i] < minDifference) {
                    minDifference = maxPresence - this.presence[i];
                }
            }
        } else {
            minDifference = 0.0;
        }

        let fullLockThreshold = 0.9;
        let confidence = Math.min(1.0, minDifference / fullLockThreshold);

        this.currentFlash = Math.max(0, this.currentFlash - dt);
        if (this.currentFullyLockedId !== maxId && maxId !== null) {
            this.currentFlash = confidence;

            if (confidence > 0.99) {
                this.selectEntityId(maxId);
            }
        }

        this.setState({
            lockingId: maxId,
            confidence: confidence,
            flash: this.currentFlash,
        });
    }

    selectEntityId(entityId) {
        this.currentFullyLockedId = entityId;
        this.currentFlash = 1.0; // Let's flash it

        this.setState({
            fullyLockedId: this.currentFullyLockedId,
            flash: this.currentFlash,
        });

        let entity = Universe.theUniverse.entityForId(this.currentFullyLockedId);

        if (entity)
            entity.onSelected();
        console.log("selectEntityId", this.currentFullyLockedId);
    }

    componentDidMount() {
        if (!Universe.load()) {
            this.newGame();
        } else {
            console.log("Loaded universe");
        }

        for (let entity of Universe.theUniverse.entities) {
            let image = new Image();
            image.src = entity.image;
        }

        this.printUniverseInfo();
    }

    printUniverseInfo() {
        console.log("Universe", Universe.theUniverse);

        let resources = {};

        for (let resource of RESOURCES) {
            resources[resource.name] = Universe.theUniverse.countResource(resource);
        }

        console.log("Resources", resources);
    }

    setDialog(dialog, modal = true) {
        if (dialog === null) {
            //full reset
            this.currentFullyLockedId = null;
            this.currentFlash = 0.5; //slight flash

            this.presence = [];
            for (let i = 0; i < AR.NUM_MARKERS; i++)
                this.presence[i] = 0.0;

            this.setState({
                currentlyDisplayedDialog: null,
                modalDialog: false,
                lockingId: null,
                fullyLockedId: this.currentFullyLockedId,
                flash: this.currentFlash,
                confidence: 0,
            });
        } else {
            this.setState({
                currentlyDisplayedDialog: dialog,
                modalDialog: modal
            });
        }

        AR.setEnabledCamera(!modal || dialog === null);
    }

    newGame() {
        console.log("New Game");
        Universe.selectBestUniverse(createDemoUniverse);
        Utils.speak("New universe");

        this.printUniverseInfo();
        this.setDialog(initialDialog(Universe.theUniverse));
    }

    render() {
        let flash = AR.isShowingCamera() ? this.state.flash : 0; //Not based on state, kind of hackish

        return <React.Fragment>
            <button
                className="menu-button"
                onClick={() => this.setDialog(
                    <TauPrimeMenu />
                )}>
                <img src={icon_menu} alt="Menu"/>
            </button>
            {this.state.currentlyDisplayedDialog ? this.state.currentlyDisplayedDialog : <div className="scanner-info"><h1>Scanning</h1></div>}
            <div className="scanner-static-background"/>
            <div className="scanner-locking-progress" style={{opacity: flash}} />
        </React.Fragment>;
    }
}

App.COMPONENT = null;