function createEntities(universe) {
    for (let mapEntityData of MAP_ENTITY_DATA) {
        universe.registerEntity(new Location(mapEntityData));
    }

    universe.registerEntity(new ScientistDataLogEntity());
}

export class AlienShip extends CelestialBody {
    constructor({attackDice, ...config}) {
        super({
            name: "Alien Ship",
            image: alien_ship,
            size: CELESTIAL_SIZE_MEDIUM,
            ...config,
        });

        this.attackDice = attackDice;
    }

    get disabled() {
        return false;
    }

    get actions() {
        return [
            {name: `Fight`, action: () => this.onAggression(), type: ACTION_TYPE_CONFLICT}
        ];
    }

    onAggression() {
        speak('They are locking weapons on us!');

        App.COMPONENT.setDialog(
            <div className="dialog">
                <div className="dialog-inner">
                    <div className="dialog-content">
                        <p>The alien ship attacks with {this.attackDice} {this.attackDice === 1 ? 'die' : 'dice'}.</p>
                        <p>Perform a standard combat round.</p>
                        <p>If the alien ship suffers at least 1 damage, the aliens are destroyed.</p>
                    </div>
                    <div className="dialog-bottom">
                        <button key="destroyed" onClick={
                            () => {
                                this.location.removeBody(this);

                                speak('Among the wreckage you found an artifact.');
                                App.COMPONENT.setDialog(
                                    <div className="dialog">
                                        <div className="dialog-inner">
                                            <div className="dialog-content">
                                                <p>Among the wreckage you found an artifact.</p> {/*TODO: Nice printing of this*/}
                                                <p>Remove the alien ship from the game.</p>
                                            </div>
                                            <div className="dialog-bottom">
                                                <button key="next" onClick={() => {
                                                    returnToScanner();
                                                }}>OK</button>
                                            </div>
                                        </div>
                                    </div>
                                );
                            }
                        }>Destroyed
                        </button>
                        <button key="notdestrotyed" onClick={() => {
                            speak('Alien ship remains intact');
                            returnToScanner();
                        }}>Not Destroyed
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

Serialisation.registerDynamicClass(AlienShip, "AlienShip");

export class Wormhole extends CelestialBody {
    constructor({destinationId = null, ...config}) {
        super({image: wormholeImage, ...config});

        this.destinationId = destinationId;
        console.assert(this.destinationId !== null, this);
    }


    get destination() {
        return Universe.theUniverse.entityForId(this.destinationId);
    }

    get disabled() {
        return false;
    }

    get actions() {
        return [
            {name: `Enter`, action: () => this.onMove(), type: ACTION_TYPE_MOVEMENT}
        ];
    }

    onMove() {
        speak('The ship gets near the wormhole, are you sure you want to enter it?');

        App.COMPONENT.setDialog(
            <div className="dialog">
                <div className="dialog-inner">
                    <div className="dialog-content">
                        <p>{this.renderImage()}</p>
                        <p>The ship gets near the wormhole, time and space starts to bend, are you sure you want to send your move to enter it?</p>
                    </div>
                    <div className="dialog-bottom">
                        <button key="OK" onClick={
                            () => {
                                let text = `You arrive at ${this.destination.nameWithHints}.`;
                                speak(text);
                                App.COMPONENT.setDialog(
                                    <div className="dialog">
                                        <div className="dialog-inner">
                                            <div className="dialog-content">
                                                <p>{this.renderImage()}</p>
                                                <p>{text} Move your ship figure there.</p>
                                                <p>Disoriented, the crew is wondering if it's even possible to go back ...</p>
                                            </div>
                                            <div className="dialog-bottom">
                                                <button key="next" onClick={() => {
                                                    returnToScanner();
                                                }}>OK</button>
                                            </div>
                                        </div>
                                    </div>
                                );
                            }
                        }>Enter
                        </button>
                        <button key="back" className="not-important" onClick={() => {
                            returnToScanner();
                        }}>Leave
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

Serialisation.registerDynamicClass(Wormhole, "Wormhole");

export function createPossibleSourceFilters(universe) {
    let possibleSourceFilters = [];

    PLANET_TYPES.forEach((planetType) => {
        possibleSourceFilters.push({
            description: `on ${planetType} planets`,
            filter: (planet) => planet.type === planetType
        });
    });

    CELESTIAL_SIZES.forEach((size) => {
        possibleSourceFilters.push({
            description: `on ${size} planets`,
            filter: (planet) => planet.size === size
        });
    });

    SECTORS.forEach((sector) => {
        possibleSourceFilters.push({
            description: `on planets in ${sector} sector`,
            filter: (planet) => planet.location.sector === sector
        });
    });

    //TODO: Blue nebula is too small ...
    [null, PLANET_TRAIT_GREEN_NEBULA, PLANET_TRAIT_PINK_NEBULA].forEach((nebula) => {
        possibleSourceFilters.push({
            description: nebula === null ? `on planets not in any nebula` : `on planets in ${nebula} nebula`,
            filter: (planet) => planet.location.nebula === nebula
        });
        possibleSourceFilters.push();
    });

    console.log("possibleSourceFilters", possibleSourceFilters.length);
    possibleSourceFilters = possibleSourceFilters.filter(sourceFilter => universe.planets.filter(sourceFilter.filter).length > 0);
    console.log("possibleSourceFilters after viability test", possibleSourceFilters.length);

    shuffle(possibleSourceFilters);
    return possibleSourceFilters;
}

export function initialDialog(universe) {
    let spaceStationLocation = universe.entityForId(universe.startingLocationId);
    let spaceStation = spaceStationLocation.bodies[0];

    let location = `Albatross is located in ${spaceStationLocation.nameWithHints}. This is where all the players start their journey.`;
    speak(location);

    return <div className="dialog">
        <div className="dialog-inner">
            <div className="dialog-content">
                <img className="detailed-planet-image" src={spaceStation.image} alt={spaceStation.name} />
                <h1>{spaceStation.name}</h1>
                <p>Two days ago we completed a the deployment of this space station in this unexplored region of our galaxy.</p>
                <p>Your goal is to be the first to collect 4 artifacts.</p>
                <p><em>{location}</em></p>
            </div>
            <div className="dialog-bottom">
                <button onClick={() => returnToScanner()} >Start game</button>
            </div>
        </div>
    </div>
}

export function createDemoUniverse() {
    let universe = new Universe({name: chance.word()});
    let dataLogs = [];

    Universe.theUniverse = universe;

    createEntities(universe);

    //
    // Place space station
    //
    let spaceStation = new SpaceStationItem();
    let spaceStationLocation = getRandomElement(universe.locations.filter(entity => entity.neighbourIds.size >= 4));
    spaceStationLocation.addBody(spaceStation);
    universe.startingLocationId = spaceStationLocation.id;

    //
    // Planets
    //
    let planetNames = PLANET_NAMES.slice();
    shuffle(planetNames);
    for (let planetType of PLANET_TYPES) {
        for (let planetSize of CELESTIAL_SIZES) {
            for (let i = 0; i < 2; i++) {
                let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

                let planet = new Planet({
                    id: `${planetType}${planetSize}${i}`,
                    name: planetNames.pop(),
                    targetSize: CELESTIAL_SIZE_MULTIPLIERS[planetSize],
                    image: PLANET_TYPE_IMAGES[planetType][planetSize],
                    type: planetType,
                    size: planetSize,
                });

                location.addBody(planet);
            }
        }
    }

    //
    // Asteroids
    //
    for (let i = 0; i < 4; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

        let item = new SimpleItem({
            id: `asteroid-${i}`,
            name: "Asteroids",
            image: asteroids_image,
            size: CELESTIAL_SIZE_SMALL,
        });

        location.addBody(item);
    }

    //
    // Black holes
    //
    for (let i = 0; i < 2; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

        let item = new SimpleItem({
            id: `blackhole-${i}`,
            name: "Black Hole",
            image: black_hole_image,
            size: CELESTIAL_SIZE_BIG,
        });

        location.addBody(item);
    }

    //
    // 2-way wormhole
    //
    let wormholeLocation1 = getRandomElement(universe.locations.filter(l => l.bodies.length < 3));
    let wormholeLocation2 = getRandomElement(universe.locations.filter(l => l.bodies.length < 3 && l !== wormholeLocation1));

    wormholeLocation1.addBody(new Wormhole({
        id: `wormhole-1`,
        name: `Wormhole C-61`,
        destinationId: wormholeLocation2.id
    }));

    wormholeLocation2.addBody(new Wormhole({
        id: `wormhole-2`,
        name: `Wormhole C-62`,
        destinationId: wormholeLocation1.id
    }));

    //
    // 1-way wormhole
    //
    getRandomElement(universe.locations.filter(l => l.bodies.length < 3 && l !== wormholeLocation1 && l !== wormholeLocation2)).addBody(new Wormhole({
        id: `wormhole-3`,
        name: `Wormhole C-63`,
        destinationId: getRandomElement(universe.locations.filter(l => l.bodies.length < 3)).id,
    }));

    //
    // Alien ship
    //
    let ships = [];
    for (let i = 0; i < 3; i++) {
        let location = getRandomElement(universe.locations.filter(entity => entity.bodies.length < 3));

        let ship = new AlienShip({
            id: `alienship-${i}`,
            attackDice: 2+i,
        });

        ships.push(ship);
        location.addBody(ship);
    }

    dataLogs.push(new SimpleLogExploration({
        text: `You have found and decoded an alien communication device. Here is what we learned: the weakest alien ship was located at ${ships[0].location.name}, the strongest was at ${ships[2].location.name} and the remainig one was at ${ships[1].location.name}. All of them carry an artifact on board.`
    }));

    //
    // Create resources
    //
    let creator = new ZoneCreator(universe);

    let possibleSources = createPossibleSourceFilters(universe);

    for (let resource of [RESOURCE_A, RESOURCE_B]) {
        const filter1 = possibleSources.pop();
        const filter2 = possibleSources.pop();

        creator.addExplorationCandidates({
            exploration: new ResourceExploration({resource: resource, count: 1}),
            surfaceMinRatio: 0.1,
            minTotal: 18,
            maxTotal: 18,
            filter: (planet) => filter1.filter(planet) || filter2.filter(planet),
        });

        dataLogs.push(new SimpleLogExploration({
            text: `${resource.plural} can be found ${filter1.description} and ${filter2.description}.`
        }));
    }

    let artifactFilter = possibleSources.pop();

    creator.addExplorationCandidates({
        exploration: new ResourceExploration({resource: RESOURCE_D, count: 1}),
        surfaceMinRatio: 0.1,
        minTotal: 8,
        maxTotal: 10,
        filter: artifactFilter.filter,
    });

    dataLogs.push(new SimpleLogExploration({
        text: `${RESOURCE_D.plural} can be found ${artifactFilter.description}.`
    }));


    //universe.dataLogs.add(DATA_LOG_BUGS_LOVE_HIGH_GRAVITY);
    //fillRandomizedDataLogs(universe);
    //fillScanningRestrictions(universe);
    //fillDataLogCards(universe);
    //fillZoneCandidatesFromLogs(universe);
    //fillDataLogExplorations(universe);

    for (let dataLog of dataLogs) {
        creator.addExplorationCandidates({
            exploration: dataLog,
            surfaceMaxRatio: 0.4,
            minTotal: 2,
            maxTotal: 3,
            filter: (planet) => true,
        });
    }

    creator.fillUniverse();


    //remove empty planets
    for (let planet of universe.planets) {
        if (planet.explorations.length === 0) {
            planet.location.removeBody(planet);
        }
    }

    Universe.theUniverse = null;

    return universe;
}