import planets28 from "../data/planets/start.png";
import {returnToScanner} from "../components/Dialog";
import App from "../components/App";
import Dialog from "../components/Dialog";
import React from "react";
import {Universe} from "../game/Universe";
import * as Serialization from "../misc/Serialisation";
import Icon from "../components/Icon";
import {RESOURCE_B} from "../data/resourceData";
import {speak} from "../misc/Utils";
import {CELESTIAL_SIZE_SMALL} from "../data/planetData";
import {CelestialBody} from "../game/map";

export class SpaceStationItem extends CelestialBody {
    constructor({id = 0, name = "Albatross space station", dataLogs = [], image = planets28, locationId, size = CELESTIAL_SIZE_SMALL} = {}) {
        super({id, name, image, locationId, size});

        this.dataLogs = dataLogs;
    }

    notifyThatWasSelected() {
        if (this.canBeSelected) {

            let dataLogs = Universe.theUniverse.remainingDataLogCards.slice(-2);

            App.COMPONENT.setDialog(<Dialog
                result={<React.Fragment>
                    <h1>Albatross space station</h1>
                    <p><em>Gain 1 gas <Icon obj={RESOURCE_B}/> and read the following out loud.</em></p>
                    <p>There are few things we have learned about the universe so far: </p>
                    <p><em>Place the following 2 data logs near the board face up, they are public knowledge.</em></p>
                    {dataLogs.map(dataLog => <div className="data-log"><img src={dataLog.image} alt={dataLog.id}/></div>)}
                    <p>Good luck!</p>
                </React.Fragment>}
                voice={'Welcome to the Albatross station'}
                buttons={[{
                    name: "Finish exploration", onClick: () => {

                        //Remove all copies of those data logs
                        let newRemainingDataLogCards =  Universe.theUniverse.remainingDataLogCards.filter(dataLog => dataLogs.indexOf(dataLog) !== -1)
                        console.assert(newRemainingDataLogCards.length === Universe.theUniverse.remainingDataLogCards - 4);
                        Universe.theUniverse.remainingDataLogCards = newRemainingDataLogCards;
                        Universe.notifyThatUniverseChanged();

                        speak(`${this.name} is now fully explored.`);

                        returnToScanner()
                    }
                }]}
            />);
        } else {
            speak(`${this.name} is fully explored.`);
        }
    }

    get canBeSelected() {
        return Universe.theUniverse.remainingDataLogCards.length > 0 && this.dataLog == null;
    }
}

Serialization.registerDynamicClass(SpaceStationItem, "SpaceStationEntity");