import React from "react";
import Exploration from "../game/Exploration";
import * as Serialization from "../misc/Serialisation";
import Icon from "../components/Icon";

export class ResourceExploration extends Exploration {
    constructor({resource, count = 1, ...config} = {}) {
        console.assert(typeof count === 'number', count);

        let providedResources = [];
        for (let i = 0; i < count; i++) {
            providedResources.push(resource);
        }

        super({
            providedResources: providedResources,
            ...config
        });

        this.resource = resource;
        this.count = count;
    }

    calculate(mission) {
        return {
            result: <React.Fragment>
                You found <Icon obj={this.resource} count={this.count} />.
            </React.Fragment>,
            voice: `You found ${this.count} ${this.count === 1 ? this.resource.name : this.resource.plural}.`,
        }
    }
}

Serialization.registerDynamicClass(ResourceExploration, "ResourceExploration");

