import {distributeCount, getRandomInt, getWeightedRandomElement, shuffle} from "../misc/Utils";

export const ZONE_CANDIDATE_PRIORITY_MUST = 0;
export const ZONE_CANDIDATE_PRIORITY_FLEX = 1;
export const ZONE_CANDIDATE_PRIORITY_FILL = 2;

export class ZoneCreator {
    constructor(universe) {
        this.universe = universe;

        this.explorationCandidates = {};

    }

    addExplorationCandidate({planet, exploration, count = 1, priority = ZONE_CANDIDATE_PRIORITY_FLEX, visible = true}) {
        if (!(planet.id in this.explorationCandidates)) {
            this.explorationCandidates[planet.id] = [];
            this.explorationCandidates[planet.id][ZONE_CANDIDATE_PRIORITY_MUST] = [];
            this.explorationCandidates[planet.id][ZONE_CANDIDATE_PRIORITY_FLEX] = [];
            this.explorationCandidates[planet.id][ZONE_CANDIDATE_PRIORITY_FILL] = [];
        }

        this.explorationCandidates[planet.id][priority].push({count, exploration, visible});
    }

    getCandidates(planet, priority) {
        if (planet.id in this.explorationCandidates) {
            return this.explorationCandidates[planet.id][priority];
        }

        return [];
    }

    addExplorationCandidatesWithDistribution({countFrequencies, explorationCreator, minTotal = 0, maxTotal, ...params}) {
        let targetResourcesNum = getRandomInt(minTotal, maxTotal);
        let counts = distributeCount(targetResourcesNum, countFrequencies);

        for (let [stashSize, numOfStashes] of Object.entries(counts)) {
            let exploration = explorationCreator(stashSize.count);
            this.addExplorationCandidates({exploration, minTotal: numOfStashes, maxTotal: numOfStashes, ...params});
        }
    }

    addExplorationCandidates({exploration, surfaceMinRatio = 0.0, surfaceMaxRatio = null, minTotal = 0, maxTotal = null, filter}) {

        let numExplorations = 0;

        let planetCounts = {};
        let planetSurfaceMins = {};
        let planetSurfaceMaxs = {};

        //
        // 1. Set planet counts, mins and maxes
        //
        for (let planet of this.universe.planets.filter(filter)) {
            planetCounts[planet.id] = 0;
            planetSurfaceMins[planet.id] = Math.ceil(planet.targetSize * surfaceMinRatio);
            if (surfaceMaxRatio === null) {
                planetSurfaceMaxs[planet.id] = null;
            } else {
                planetSurfaceMaxs[planet.id] = Math.max(planetSurfaceMins[planet.id], Math.floor(planet.targetSize * surfaceMaxRatio));
            }
        }

        //
        // 2. Make sure we have a surfaceMinRatio on all planets
        //
        for (let planet of shuffle(this.universe.planets.filter(filter))) { //TODO: Random weighted order?
            planetCounts[planet.id] = 0;

            while (planetCounts[planet.id] < planetSurfaceMins[planet.id]) {

                if (maxTotal !== null && numExplorations >= maxTotal) { //TODO: Maybe remove this restriction?
                    break;
                }

                this.addExplorationCandidate({
                    planet,
                    exploration,
                    priority: ZONE_CANDIDATE_PRIORITY_MUST
                });

                numExplorations += 1;
                planetCounts[planet.id] += 1;
            }
        }

        //
        // 3. Make sure we have a surfaceMaxRatio on all planets
        //
        for (let planet of shuffle(this.universe.planets.filter(filter))) { //TODO: Random weighted order?
            if (planetSurfaceMaxs[planet.id] !== null) {
                let targetZones = planetSurfaceMaxs[planet.id]; //getRandomInt(planetSurfaceMins[planet.id], planetSurfaceMaxs[planet.id]);

                while (planetCounts[planet.id] < targetZones) {

                    if (maxTotal !== null && numExplorations >= maxTotal) {
                        break;
                    }

                    this.addExplorationCandidate({
                        planet,
                        exploration,
                        priority: numExplorations < minTotal ? ZONE_CANDIDATE_PRIORITY_MUST : ZONE_CANDIDATE_PRIORITY_FLEX
                    });

                    numExplorations += 1;
                    planetCounts[planet.id] += 1;
                }
            }
        }

        //
        // 4. Make sure we met the minTotal (but don't attack the planets with maximum)
        // 5. Randomize target total and add planets until we have it
        //
        let targetNumExplorations = (maxTotal !== null) ? getRandomInt(minTotal, maxTotal) : minTotal;
        while (numExplorations < targetNumExplorations) {

            //Randomize planets with most free space first ...
            let possiblePlanets = this.universe.planets.filter(filter);
            let possibleNotAtMax = possiblePlanets.filter(planet => planetSurfaceMaxs[planet.id] === null || planetCounts[planet.id] < planetSurfaceMaxs[planet.id]);

            if (possibleNotAtMax.length === 0) {
                break;
            }

            let possibleFreeWeights = possibleNotAtMax.map(planet => {
                return {
                    planet,
                    weight:
                        Math.max(
                            0.1,
                            planet.targetSize - (this.getCandidates(planet, ZONE_CANDIDATE_PRIORITY_MUST).length + this.getCandidates(planet, ZONE_CANDIDATE_PRIORITY_FLEX).length)
                        )
                }
            });

            let planet = getWeightedRandomElement(possibleFreeWeights).planet;

            this.addExplorationCandidate({
                planet,
                exploration,
                priority: numExplorations < minTotal ? ZONE_CANDIDATE_PRIORITY_MUST : ZONE_CANDIDATE_PRIORITY_FLEX
            });

            numExplorations += 1;
            planetCounts[planet.id] += 1;
        }


        if (numExplorations < minTotal) {
            console.error("Unable to reach minTotal", numExplorations, targetNumExplorations, {exploration, surfaceMinRatio, surfaceMaxRatio, minTotal, maxTotal, filter});
        }


        if (maxTotal !== null && numExplorations > maxTotal) {
            console.info("More than max total", numExplorations, maxTotal);
        }
    }


    fillUniverse() {
        for (let planet of this.universe.planets) {
            this.fillPlanetaryExplorations(planet);
        }

        console.log("Creator finished", this);
    }


    fillPlanetaryExplorations(planet) {
        let explorations = [];

        const addCandidate = ({exploration, count = 1}) => {
            for (let i = 0; i < count; i++) {
                explorations.push(exploration);
            }
        };

        for (let zoneCandidate of this.getCandidates(planet, ZONE_CANDIDATE_PRIORITY_MUST)) {
            addCandidate(zoneCandidate);
        }

        let flexCandidates = this.getCandidates(planet, ZONE_CANDIDATE_PRIORITY_FLEX).slice();
        shuffle(flexCandidates);
        for (let zoneCandidate of flexCandidates) {

            if (explorations.length > planet.targetSize) {
                break;
            }

            addCandidate(zoneCandidate);
        }

        const fillCandidates = this.getCandidates(planet, ZONE_CANDIDATE_PRIORITY_FILL).slice();
        shuffle(fillCandidates);
        for (let i = explorations.length; i < planet.targetSize; i++) {
            if (fillCandidates.length === 0) {
                break;
            }
            addCandidate(fillCandidates.pop());
        }

        shuffle(explorations);
        planet.initExplorations({explorations});
    }
}