export const AR = {};

const cameraUpdateListeners = [];

export function registerOnCameraUpdateListener(func) {
    console.assert(!cameraUpdateListeners.includes(func));
    cameraUpdateListeners.push(func);
}

export function removeOnCameraUpdateListener(func) {
    console.assert(cameraUpdateListeners.includes(func));
    cameraUpdateListeners.splice(cameraUpdateListeners.indexOf(func), 1);
}

(function () {
    AR.NUM_MARKERS = 64;
    let cameraIsEnabled = false;
    let markers = [];

    const TIME_UNTIL_REST = 10.0;
    const REST_ACCELERATION_THRESHOLD = 0.1;
    let lastCameraShowingStatus = false;
    let timeSinceMotion = 0;

    window.ondevicemotion = function (event) {
        if (event.acceleration.x > REST_ACCELERATION_THRESHOLD ||
            event.acceleration.y > REST_ACCELERATION_THRESHOLD ||
            event.acceleration.z > REST_ACCELERATION_THRESHOLD) {
            timeSinceMotion = 0;
            AR.updateCameraStatus();
        }
    };

    AR.setEnabledCamera = function (enabled) {
        cameraIsEnabled = enabled;
        AR.updateCameraStatus();
    };

    AR.getCurrentMarkerVisibility = function () {
        let visibility = {};

        for (let i = 0; i < AR.NUM_MARKERS; i++) {
            visibility[i] = markers[i].object3d.visible;
        }

        return visibility;
    };

    //
    // Modifying the library function to inverse colors
    //
    window.ARController.prototype._copyImageToHeap = function (image) {
        if (!image) {
            image = this.image;
        }

        if ((image.nodeName === 'IMG' && image.width > image.height) ||
            (image.nodeName === 'VIDEO' && image.videoWidth > image.videoHeight)) {
            // if landscape
            this.ctx.drawImage(image, 0, 0, this.canvas.width, this.canvas.height); // draw video
        } else {
            // if portrait
            this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
            const scale = this.canvas.height / this.canvas.width;
            const scaledHeight = this.canvas.width * scale;
            const scaledWidth = this.canvas.height * scale;
            const marginLeft = (this.canvas.width - scaledWidth) / 2;
            this.ctx.drawImage(image, marginLeft, 0, scaledWidth, scaledHeight); // draw video
        }

        const imageData = this.ctx.getImageData(0, 0, this.canvas.width, this.canvas.height);
        const data = imageData.data;

        //
        // Modification start
        //
        for (var q = 0; q < data.length; q += 4) {
            data[q + 0] = 255 - data[q + 0];
            data[q + 1] = 255 - data[q + 1];
            data[q + 2] = 255 - data[q + 2];
        }
        //
        // Modification end
        //

        if (this.dataHeap) {
            this.dataHeap.set(data);
            return true;
        }
        return false;
    };

    AR.initializeAR = function () {
        let renderer = new window.THREE.WebGLRenderer({
            // antialias  : true,
            alpha: true
        });

        renderer.setClearColor(new window.THREE.Color('lightgrey'), 0);
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.domElement.style.position = 'absolute';
        renderer.domElement.style.top = '0px';
        renderer.domElement.style.left = '0px';

        document.body.appendChild(renderer.domElement);

        let scene = new window.THREE.Scene();
        let camera = new window.THREE.Camera();
        scene.add(camera);

        let arToolkitSource = new window.THREEx.ArToolkitSource({
            sourceType: 'webcam',
        });

        arToolkitSource.init(function onReady() {
            onResize()
        });

        // handle resize
        window.addEventListener('resize', function () {
            onResize()
        });

        function onResize() {
            arToolkitSource.onResizeElement();
            arToolkitSource.copyElementSizeTo(renderer.domElement);
            renderer.domElement.style.position = 'fixed';
            arToolkitSource.domElement.style.position = 'fixed';
            if (arToolkitContext.arController !== null) {
                arToolkitSource.copyElementSizeTo(arToolkitContext.arController.canvas);
                arToolkitContext.arController.canvas.style.position = 'fixed';

                let otherElement = document.getElementById('root');
                if (window.innerWidth > window.innerHeight) {
                    //landscape
                    otherElement.style.height = arToolkitSource.domElement.style.height;
                    otherElement.style.marginTop = arToolkitSource.domElement.style.marginTop
                }
                else {
                    //portrait
                    otherElement.style.height = arToolkitSource.domElement.style.height;
                    otherElement.style.marginTop = "0";
                }
            }
        }

        let arToolkitContext = new window.THREEx.ArToolkitContext({
            cameraParametersUrl: 'data/camera_para.dat',
            detectionMode: 'mono_and_matrix',
            debug: false,
            matrixCodeType: '3x3',
            maxDetectionRate: 30,
            canvasWidth: 80 * 3,
            canvasHeight: 60 * 3,
        });

        // initialize it
        arToolkitContext.init(function onCompleted() {
            camera.projectionMatrix.copy(arToolkitContext.getProjectionMatrix());
        });

        for (let i = 0; i < AR.NUM_MARKERS; i++) {
            let markerRoot = new window.THREE.Group();
            scene.add(markerRoot);

            markers[i] = new window.THREEx.ArMarkerControls(arToolkitContext, markerRoot, {
                type: 'barcode',
                barcodeValue: i,
            })
        }

        let animationFrame = 0;
        let lastTimeMilliseconds = null;

        function animate(nowMilliseconds) {
            animationFrame = requestAnimationFrame(animate);

            // measure time
            lastTimeMilliseconds = lastTimeMilliseconds || nowMilliseconds - 1000 / 60;
            let deltaMilliseconds = Math.min(200, nowMilliseconds - lastTimeMilliseconds);
            lastTimeMilliseconds = nowMilliseconds;
            let dt = deltaMilliseconds / 1000;

            if (arToolkitSource.ready)
                arToolkitContext.update(arToolkitSource.domElement);
            renderer.render(scene, camera);

            timeSinceMotion += dt;
            AR.updateCameraStatus();

            cameraUpdateListeners.forEach(listener => listener(dt));
        }

        AR.isShowingCamera = function () {
            return cameraIsEnabled && timeSinceMotion < TIME_UNTIL_REST;
        }

        AR.updateCameraStatus = function () {
            let shouldShowCamera = AR.isShowingCamera();
            if (lastCameraShowingStatus !== shouldShowCamera) {
                if (shouldShowCamera) {
                    document.querySelector("body>canvas") && document.querySelector("body>canvas").removeAttribute("class");
                    document.querySelector("body>video") && document.querySelector("body>video").removeAttribute("class");

                } else {
                    document.querySelector("body>canvas") && document.querySelector("body>canvas").setAttribute("class", "hidden-canvas");
                    document.querySelector("body>video") && document.querySelector("body>video").setAttribute("class", "hidden-canvas");
                }

                cancelAnimationFrame(animationFrame);

                if (shouldShowCamera) {
                    animationFrame = requestAnimationFrame(animate);
                } else {
                    animationFrame = 0;
                }

                lastCameraShowingStatus = shouldShowCamera;
            }
        };

        AR.setEnabledCamera(true);
    }
})();
      