import pattern60 from "../data/icons/pattern_60.png";
import {createDataLogResultVoiceButtons} from "./dataLogs";
import {Universe} from "../game/Universe";
import {returnToScanner} from "../components/Dialog";
import Dialog from "../components/Dialog";
import * as Serialisation from "../misc/Serialisation";
import React from "react";
import {Entity} from "../game/Entity";
import App from "../components/App"

export class ScientistDataLogEntity extends Entity {
    constructor() {
        super({id: 60, name:"Scientist data log", image: pattern60});
    }

    onSelected() {
        if (Math.random() > 0.5) {
            let {result, voice, buttons} = createDataLogResultVoiceButtons({universe: Universe.theUniverse, reason: 'Scientist researched something significant', onOK: () => returnToScanner()});

            App.COMPONENT.setDialog(<Dialog
                result={result}
                voice={voice}
                buttons={buttons}
            />, false);
        } else {
            App.COMPONENT.setDialog(<Dialog
                result={"Scientist did not learn anything new, you have to keep on trying."}
                buttons={[{name: "OK", onClick: () => {returnToScanner()}}]}
            />, false);
        }
    }
}

Serialisation.registerDynamicClass(ScientistDataLogEntity, "ScientistDataLogEntity");

