import Exploration, {exploreAndReturnToScanner, StaticExploration} from "../game/Exploration";
import Dialog, {returnToScanner} from "../components/Dialog";
import App from '../components/App'
import React from "react";
import {
    CREW_CAPTAIN,
    CREW_MONKEY,
    CREW_PILOT,
    CREW_SCIENTIST,
    CREW_TECHNICIAN, EQUIPMENT_BUGGY,
    EQUIPMENT_GUN,
    EQUIPMENT_MEDKIT,
    EQUIPMENT_SCANNER,
    EQUIPMENT_SUIT,
    RESOURCE_A,
    RESOURCE_B,
} from "../data/resourceData";
import * as Serialisation from "../misc/Serialisation";
import {getRandomElement} from "../misc/Utils";
import {Universe} from "../game/Universe";
import Icon from "../components/Icon";


//
//
//
let exhaustTo = (object, to) => <p>You may exhaust a {object.name} <Icon obj={object} /> to {to}.</p>;
let exhaustOnShipTo = (object, to) => <p>You may exhaust a {object.name} <Icon obj={object} /> on board your ship, to {to}.</p>;
let exhaustEnvironSuitToSaveInSuits = () => exhaustTo(EQUIPMENT_SUIT, <React.Fragment>save each crew member with <Icon obj={EQUIPMENT_SUIT} /> equipped (no matter if it's ready or exhausted)</React.Fragment>);

//
// Exploration - Nothing found
//

export const EXPLORATION_NOTHING = new StaticExploration({id: "EXPLORATION_NOTHING", calculate: (zone) => {
    return {
        result: <React.Fragment>
            <p>You spent hours exploring the planet and haven't found anything of interest.</p>
            <p>If you don't have a buggy <Icon obj={EQUIPMENT_BUGGY} /> exhaust one team member.</p>
        </React.Fragment>,
        voice: `You spent hours exploring and haven't found anything of interest. Exhaust one team member.`,
    }
}});

//
// Exploration - Simple before and after
//

export class FullyExploredExploration extends Exploration{
    constructor({...config} = {}) {
        super(config);
    }

    calculate(zone) {
        let universe = Universe.theUniverse;
        let definition = universe.definition;
        let dataLog = getRandomElement(definition.dataLogs);
        let buttons = [
            {name: "OK", onClick: () => {returnToScanner()}},
        ];

        return {
            result: <React.Fragment>
                {zone.planet.name} is now fully explored.
                Your exploration of this planet allowed you to learn:<br/><br/>
                <div className="data-log">Acquire data log {dataLog.id}.</div>
            </React.Fragment>,
            voice: `${zone.planet.name} is now fully explored.`,
            buttons: buttons,
        }
    }
}

Serialisation.registerDynamicClass(FullyExploredExploration, "FullyExploredExploration");

export class SimpleBeforeExploration extends Exploration{
    constructor({result, voice, ...config}) {
        super(config);

        this.result = result;
        this.voice = voice;
    }

    calculate(zone) {
        return {
            result: this.result,
            voice: this.voice,
            buttons: [{name: "OK", onClick: () => {
                zone.markExplored();
                Universe.theUniverse.entityForId(zone.planetId).notifyThatWasSelected();
            }}],
        }
    }
}

Serialisation.registerDynamicClass(SimpleBeforeExploration, "SimpleBeforeExploration");

//
// Exploration - Meteor shower
//

export let EXPLORATION_METEOR_SHOWER = new StaticExploration({id: "EXPLORATION_METEOR_SHOWER", providedResources: [RESOURCE_A, RESOURCE_A], calculate: (zone) => {
    return {
        result: <React.Fragment>
            <p>While exploring the {zone.name}, your team was caught in a meteor shower, they acquired 2 crystals <Icon obj={RESOURCE_A} /> but immediately died.</p>
            {exhaustOnShipTo(CREW_PILOT, 'swoop in and save everyone')}
            {exhaustOnShipTo(EQUIPMENT_GUN, 'shoot down the meteors as they enter the atmosphere')}
        </React.Fragment>,
        voice: `Danger, incoming meteor shower`,
    }
}});

