Serialisation.registerDynamicClass(FireExploration, "FireExploration");

//
// Exploration - Magnesium
//

export class MagnesiumExploration extends Exploration{
    constructor({...config} = {}) {
        super({
            providedResources: [RESOURCE_A, RESOURCE_A, RESOURCE_A],
            ...config
        });
    }

    calculate(zone) {
        let riskIt = (probability) => {
            let successful = Math.random() < probability;
            if (successful) {
                App.COMPONENT.setDialog(<Dialog voice={`You acquired 3 crystals.`} result={<React.Fragment>You acquired 3 <Icon obj={RESOURCE_A} /></React.Fragment>} buttons={[{name: "OK", onClick: () => exploreAndReturnToScanner(zone)}]} />);
            } else {
                App.COMPONENT.setDialog(<Dialog result="The magnesium exploded, your team has been annihilated, remove them from the game." buttons={[{name: "OK", onClick: () => exploreAndReturnToScanner(zone)}]} />);
            }
        };

        return {
            result: <React.Fragment>
                <p>You have encountered a volatile magnesium {zone.type.name.toLowerCase()}.</p>
                <p>There are 3 crystals <Icon obj={RESOURCE_A} /> to acquire, but it's dangerous to get. If you have a scanner <Icon obj={EQUIPMENT_SCANNER} /> and a technician <Icon obj={CREW_TECHNICIAN} /> in your team, you might actually manage to do it.</p>
                <p>How do you want to approach it? (Percentages are the percent of success):</p>
            </React.Fragment>,
            voice: `You have encountered a volatile magnesium ${zone.type.name}`,
            buttons: [
                {name: "Technician with a scanner - 90%", onClick: () => riskIt(0.9)},
                {name: "Technician  - 70%", onClick: () => riskIt(0.7)},
                {name: "Someone else with a scanner - 50%", onClick: () => riskIt(0.5)},
                {name: "Someone else - 10%", onClick: () => riskIt(0.1)},
                {name: "A monkey - 30%?", onClick: () => riskIt(0.3)},
                {name: "Back off", onClick: () => returnToScanner(zone)},
            ],
        }
    }
}

Serialisation.registerDynamicClass(MagnesiumExploration, "MagnesiumExploration");
