import Exploration from "./Exploration";
import {EQUIPMENT_BUGGY} from "../data/resourceData";
import Icon from "../components/Icon";


class LandingExploration extends Exploration {
    calculate(mission) {
        let planet = mission.planet;
        return {
            result: <div key="intro">
                <p>You are landing on {planet.name}, it's a {planet.size} {planet.type} planet.</p>
                <p>Select your team and equipment for this exploration mission.</p>
            </div>,
            voice: `Prepare for landing`
        }
    }
}

