import React from 'react';
import Exploration, {exploreAndReturnToScanner} from "../game/Exploration";
import './dataLogs.css';
import * as Serialisation from "../misc/Serialisation";
import {Universe} from "../game/Universe";
import {capitalizeFirst} from "../misc/Utils";

export function createDataLogResultVoiceButtons({universe, reason, onOK}) {
    if (universe.remainingDataLogCards.length > 0) {
        let dataLog = universe.remainingDataLogCards.pop();

        return {
            result: <React.Fragment>{reason}<br/><br/><br/>
                <div className="data-log">Acquire data log {dataLog.id}. <br/><br/><img src={dataLog.image} alt={"data log"} /></div>
            </React.Fragment>,
            voice: `Data log found`,
            buttons: [{
                name: "OK",
                onClick: onOK,
            }],
        }
    } else {
        return {
            result: <React.Fragment>{reason}<br/><br/><br/>
                <div className="data-log">There are no things left to learn.</div>
            </React.Fragment>,
            voice: `There are no things left to learn`,
            buttons: [{
                name: "OK",
                onClick: onOK,
            }],
        };
    }
}

class DataLogExploration extends Exploration {
    constructor({reason = `???`, ...config} = {}) {
        super(config);

        this.reason = reason;
    }

    calculate(zone) {
        return createDataLogResultVoiceButtons({
            universe: Universe.theUniverse,
            reason: this.reason,
            onOK: () => exploreAndReturnToScanner(zone)
        });
    }
}

export function fillDataLogExplorations(creator) {
    creator.addExplorationCandidates({
        exploration: new DataLogExploration({reason: `You have encountered a dead explorer, and with him his data log.`}),
        minTotal: 20,
        maxTotal: 30,
        filter: (planet) => true,
    });
}

Serialisation.registerDynamicClass(DataLogExploration, "DataLogExploration");


export class SimpleLogExploration extends Exploration {
    constructor({text, ...config} = {}) {
        super(config);
        this.text = text;
    }

    calculate(mission) {
        return {
            result: <React.Fragment>
                {capitalizeFirst(this.text)}
            </React.Fragment>,
            voice: 'Data log found',
        }
    }
}

Serialisation.registerDynamicClass(SimpleLogExploration, "SimpleLogExploration");

