import React from 'react';
import UniverseInfo from './UniverseInfo';
import {Universe} from "../game/Universe";
import {returnToScanner} from "./Dialog";
import App from "./App"

export default class TauPrimeMenu extends React.Component {
    render() {
        return <div className="dialog">
            <div className="dialog-inner">
                <div className="dialog-content">
                    <h1>Main Menu</h1>
                    <p>Universe: {Universe.theUniverse.name}</p>
                    <button onClick={() => App.COMPONENT.setDialog(<UniverseInfo />)}>info</button>
                    <button onClick={() => App.COMPONENT.newGame()}>new game</button>
                    <button className="not-important" onClick={() => returnToScanner()}>back</button>
                </div>
                <div className="dialog-bottom" />
            </div>
        </div>
    }
}