import Chance from "chance";

export const chance = new Chance();

window.Array.prototype.peek = function () {
    return this[this.length - 1];
};

/**
 * The de-facto unbiased shuffle algorithm is the Fisher-Yates (aka Knuth) Shuffle.
 * See https://github.com/coolaj86/knuth-shuffle
 */
export function shuffle(array) {
    var currentIndex = array.length, temporaryValue, randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {

        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
    }

    return array;
}

/**
 * Returns a random number between min (inclusive) and max (exclusive)
 */
export function getRandomArbitrary(min, max) {
    return Math.random() * (max - min) + min;
}

/**
 * Returns a random integer between min (inclusive) and max (inclusive)
 * Using Math.round() will give you a non-uniform distribution!
 */
export function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

/**
 * Returns a random item from array
 */
export function getRandomElement(array) {
    console.assert(array.length > 0, "Array should not be empty");
    return array[Math.floor(Math.random() * array.length)];
}

/**
 *
 */
export function extractRandomElement(array) {
    console.assert(array.length > 0, "Array should not be empty");
    let index = Math.floor(Math.random() * array.length);
    return array.splice(index, 1);
}

/**
 * Returns a random item from array, requires weight attribute on elements
 */
export function getWeightedRandomElement(arr, weightAttribute = 'weight') {

    let sumOfWeights = arr.reduce(function (sum, obj) {
        return sum + obj[weightAttribute];
    }, 0); //TODO: this could be added to the array ...
    //console.log("sumOfWeights", sumOfWeights);

    console.assert(!Number.isNaN(sumOfWeights) && typeof sumOfWeights === 'number');

    let random = Math.random() * sumOfWeights;
    //console.log("random", random);

    for (let i = 0; i < arr.length; i++) {
        random -= arr[i][weightAttribute];
        if (random <= 0) {
            //console.log("i", i);
            return arr[i];
        }
    }

    console.error("No element found", arr, sumOfWeights, random);
}


/**
 *
 */
export function speak(text) {
    console.log(text);
    window.responsiveVoice.speak(text);
}

/**
 *
 */
export function capitalizeFirst(text) {
    return text.replace(/^\w/, c => c.toUpperCase());
}

/**
 *
 */
export function haveCommonElement(arr1, arr2) {
    for (let e1 of arr1) {
        if (arr2.indexOf(e1) !== -1) {
            return true;
        }
    }

    return false;
}

/**
 *
 */
export function verifyKeyInObject(key, obj) {
    if (!(key in obj)) {
        console.error("Unknown key", key, obj)
    }
}

/**
 *
 */
export function arrayUnique(array) {
    let a = array.concat();
    for (let i = 0; i < a.length; ++i) {
        for (let j = i + 1; j < a.length; ++j) {
            if (a[i] === a[j])
                a.splice(j--, 1);
        }
    }

    return a;
}

/**
 *
 * @param array1
 * @param array2
 * @returns {SEA3D.Stream|T[]|string}
 */
export function arrayUnion(array1, array2) {
    return arrayUnique([...array1, ...array2]);
}

/**
 *
 * @param array1
 * @param array2
 * @returns {*}
 */
export function arrayIntersection(array1, array2) {
    return array1.filter(function (n) {
        return array2.indexOf(n) !== -1;
    });
}

/**
 *
 * @param value
 * @param typeCheck
 * @returns {*}
 */
export function ensureArrayOrNull(value, typeCheck = null) {
    if (value != null) {
        if (!Array.isArray(value)) {
            value = [value];
        }

        if (typeCheck) {
            value.forEach(function(element) {
                console.assert(typeCheck(element), "All elements of an array should be of given type", value);
            });
        }
    }

    return value;
}

/**
 *
 * @param value
 * @param typeCheck
 * @returns {*}
 */
export function ensureArrayOfArraysOrNull(value, typeCheck = null) {
    if (value != null) {
        if (!Array.isArray(value)) {
            value = [[value]];
        } else if (Array.isArray(value) && (value.length === 0 || !Array.isArray(value[0]))) {
            value = [value];
        }

        if (typeCheck) {
            value.forEach(function(subArray) {
                subArray.forEach(function(element) {
                    console.assert(typeCheck(element), "All elements of an array should be of given type", subArray);
                });
            });
        }
    }

    return value;
}


/**
 *
 * @param targetCount
 * @param countFrequencies
 */
export function distributeCount(targetCount, countFrequencies) {
    let ret = {};
    let check = ([size, frequency]) => size <= targetCount;
    while (targetCount > 0) {
        let possibilities = Object.entries(countFrequencies).filter(check);
        let currentCacheSize = getWeightedRandomElement(possibilities, 1)[0];
        targetCount -= currentCacheSize;
        if (!ret.hasOwnProperty(currentCacheSize)) {
            ret[currentCacheSize] = 0;
        }
        ret[currentCacheSize] += 1;
    }
    return ret;
}

export function isInstanceOfAny(obj, classes) {
    for (let classObj of classes) {
        if (obj instanceof classObj) {
            return true;
        }
    }

    return false;
}

window.String.prototype.replaceLast = function(find, replace) {
    const index = this.lastIndexOf(find);

    if (index >= 0) {
        return this.substring(0, index) + replace + this.substring(index + find.length);
    }

    return this.toString();
};