import React from 'react';
import './Dialog.css';
import {speak} from "../misc/Utils";
import App from "./App";

export let returnToScanner = () => {
    App.COMPONENT.setDialog(null);
};

export default class Dialog extends React.Component {
    render() {
        speak(this.props.voice ? this.props.voice : this.props.result);

        let buttonComponents = this.props.buttons.map((buttonDefinition, index) =>
            <button key={index} onClick={() => buttonDefinition.onClick()}>
                {buttonDefinition.name}
            </button>
        );

        return <div className="dialog">
            <div className="dialog-inner">
                <div className="dialog-content">{this.props.result}</div>
                <div className="dialog-bottom">{buttonComponents}</div>
            </div>
        </div>
    };
}

Dialog.defaultProps = {
    buttons: [{name: "OK", onClick: () => {returnToScanner()}}],
};