import {isInstanceOfAny} from "./Utils";

const REGISTERED_STATIC_CLASSES = new Set();
const REGISTERED_DYNAMIC_CLASSES = {};
const STATIC_OBJECT_REPOSITORY = {};

export function registerStaticClass(classObject) {
    console.assert(!REGISTERED_STATIC_CLASSES.has(classObject), `Duplicate static class`, classObject);
    REGISTERED_STATIC_CLASSES.add(classObject);
}

export function registerDynamicClass(classObject, classId = null) {
    console.assert(classId, `Class ID not supplied`, classId, classObject);
    console.assert(!(classId in REGISTERED_DYNAMIC_CLASSES), `Duplicate classID ${classId} for class`, classObject);
    REGISTERED_DYNAMIC_CLASSES[classId] = classObject;
}

export function registerStaticObject(id, obj) {
    console.assert(isInstanceOfAny(obj, REGISTERED_STATIC_CLASSES), `Registering a static object without first registering it's class`, id, obj, REGISTERED_STATIC_CLASSES);
    console.assert(!(id in STATIC_OBJECT_REPOSITORY), `Duplicate ID ${id}`, `existing object`, STATIC_OBJECT_REPOSITORY[id], `new object`, obj);
    STATIC_OBJECT_REPOSITORY[id] = obj;
}

export function serialize(value) {
    return JSON.stringify(value, function(name, value) {
        for (let classObject of REGISTERED_STATIC_CLASSES) {
            if (value instanceof classObject) {
                return {
                    _so: value.id,
                }
            }
        }

        for (let [classId, classObject] of Object.entries(REGISTERED_DYNAMIC_CLASSES)) {
            if (value instanceof classObject) {
                if (classId === "Set") {
                    value = {data: Array.from(value)};
                }
                return {
                    _do: classId,
                    ...value
                }
            }
        }

        if (typeof value === 'object' && value !== null) {
            if (value.constructor !== Array && value.constructor !== Object) {
                console.error(value, 'is of non registered class', value.constructor);
            }
        }

        return value;
    });
}

export function deserialize(json) {
    return JSON.parse(json, function(name, value) {
        if (value === null) {
            return value;
        }

        if (typeof value === 'object') {
            if ("_do" in value) {
                let classId = value._do;
                console.assert(classId in REGISTERED_DYNAMIC_CLASSES, `${classId} is an unknown class id.`);

                if (classId === "Set") {
                    return new REGISTERED_DYNAMIC_CLASSES[classId](value.data);
                } else {
                    return new REGISTERED_DYNAMIC_CLASSES[classId](value);
                }
            } else if (typeof value === 'object' && "_so" in value) {
                let id = value._so;

                if (id === null) {
                    return null;
                }

                console.assert(id in STATIC_OBJECT_REPOSITORY, `Unknown ID ${id}`);
                return STATIC_OBJECT_REPOSITORY[id];
            }
        }

        return value;
    });
}

export class StaticObject {
    constructor({id = null}) {
        console.assert(id !== null);
        this.id = id;

        registerStaticObject(id, this);
    }
}

registerStaticClass(StaticObject);
registerDynamicClass(Set, "Set");