import * as Serialisation from "../misc/Serialisation";

import {Planet} from "./Planet";
import {Location} from "./map";
import {RESOURCES} from "../data/resourceData";

export class Universe {

    constructor({name = "", startingLocationId, entitiesMap = {}, definition = {}, dataLogs = new Set(), remainingDataLogCards = []} = {}) {
        this.name = name;
        this.entitiesMap = entitiesMap;
        this.startingLocationId = startingLocationId;
        this.definition = definition;
        this.dataLogs = dataLogs;
        this.remainingDataLogCards = remainingDataLogCards;
    }

    get locations() {
        let ret = [];

        for (let entity of Object.values(this.entitiesMap)) {
            if (entity instanceof Location) {
                ret.push(entity);
            }
        }

        return ret;
    }

    get planets() {
        let ret = [];
        for (let body of this.celestialBodies) {
            if (body instanceof Planet) {
                ret.push(body);
            }
        }
        return ret;
    }

    get celestialBodies() {
        let ret = [];
        for (let entity of this.locations) {
            for (let item of entity.bodies) {
                ret.push(item);
            }
        }
        return ret;
    }

    get entities() {
        return Object.values(this.entitiesMap);
    }

    registerEntity(entity) {
        console.assert(!(entity.id in this.entitiesMap), "Entity already registered", entity.id, entity, this.entitiesMap[entity.id]);
        return this.entitiesMap[entity.id] = entity;
    }

    entityForId(id) {
        return this.entitiesMap[id];
    }

    bodyForId(id) {
        let possibleBodies = this.celestialBodies.filter(item => item.id === id);
        console.assert(possibleBodies.length <= 1, "Duplicate map item ids", id);
        return possibleBodies.length > 0 ? possibleBodies[0] : null;
    }

    countResource(resource) {
        let provided = 0;
        let required = 0;

        for (let planet of this.planets) {
            for (let exploration of planet.explorations) {
                for (let planetResource of exploration.providedResources) {
                    if (planetResource === resource) {
                        provided += 1;
                    }
                }

                for (let planetResource of exploration.requiredResources) {
                    if (planetResource === resource) {
                        required += 1;
                    }
                }
            }
        }
        return {provided, required};
    }

    get fitness() {
        let fitness = 100;

        for (let resource of RESOURCES) {
            let {required, provided} = this.countResource(resource);

            let distance = Math.max(
                0,
                required * 2 - provided,
            );

            fitness -= distance;
        }

        return fitness;
    }

    matchingPlanets(filter) {
        let possiblePlanets = [];

        for (let planet of this.planets) {
            if (filter.isAcceptable({planet, universe: this})) {
                possiblePlanets.push(planet);
            }
        }

        return possiblePlanets;
    }

    matchingZoneLocations(filter) {
        let possibleZoneLocations = [];

        for (let planet of this.planets) {
            for (let zoneLocation of planet.matchingZoneLocations(filter)) {
                if (!planet.getZone(zoneLocation)) {
                    possibleZoneLocations.push([planet, zoneLocation]);
                }
            }
        }

        return possibleZoneLocations;
    }

    static save() {
        localStorage.universe = Serialisation.serialize(this.theUniverse);
    }

    static load() {
        if (!localStorage.universe)
            return null;
        Universe.theUniverse = Serialisation.deserialize(localStorage.universe);
        Universe.notifyThatUniverseChanged();
        return this.theUniverse;
    }

    static selectBestUniverse(universeCreationFunction) {
        console.log("Creating universe ...");

        let UNIVERSE_CANDIDATES = 1;
        let candidates = [];

        //
        // Create candidates
        //
        for (let i = 0; i < UNIVERSE_CANDIDATES; i++) {
            let candidate = universeCreationFunction();
            candidates.push(candidate);
        }

        //
        // Sort candidates according to their fitness in descending order
        //
        candidates.sort(function (a, b) {
            return (b.fitness - a.fitness);
        });

        console.log("Candidates", candidates);

        Universe.theUniverse = candidates[0];
        Universe.notifyThatUniverseChanged();
    }

    static addTheUniverseListener(f) {
        Universe.universeListeners.push(f);
    }

    static notifyThatUniverseChanged() {
        this.save();

        for (let l of this.universeListeners) {
            l();
        }
    }
}

Universe.theUniverse = null;
Universe.universeListeners = [];

Serialisation.registerDynamicClass(Universe, "Universe");
