import {StaticObject} from "../misc/Serialisation";

export default class Resource extends StaticObject {
    constructor({name, plural, image, isCrew = false, isEquipment = false, ...config}) {
        super(config);
        this.name = name;
        this.plural = plural;
        this.image = image;
        this.isCrew = isCrew;
        this.isEquipment = isEquipment;

        console.assert(typeof this.isCrew === "boolean");
        console.assert(typeof this.isEquipment === "boolean");
    }
}
